.reviews {
  background-color: $color-default-background;

  .container {
    padding: 120px 340px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include vp-1199 {
      padding: 110px 100px 100px;
    }

    @include vp-767 {
      padding: 75px 47px 70px;
    }
  }

  h2 {
    color: $color-sapphire;
    margin-top: 0;
    margin-bottom: 35px;
    font-weight: 600;
    font-size: 54px;
    line-height: normal;
    letter-spacing: 1.62px;

    @include vp-1199 {
      font-size: 42px;
      letter-spacing: 1.26px;
      margin-bottom: 42px;
    }

    @include vp-767 {
      font-size: 32px;
      letter-spacing: 0.96px;
      margin-bottom: 42px;
    }
  }
}

.reviews__slider {
  position: relative;
  width: 560px;

  @include vp-1199 {
    width: 566px;
  }

  @include vp-767 {
    width: 100%;
  }
}

.reviews__items {
  display: flex;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.reviews__items .swiper-wrapper {
  box-sizing: content-box;
}

.reviews__slider-btn-next,
.reviews__slider-btn-prev {
  position: absolute;
  z-index: 10;
  top: 38%;

  @include vp-1199 {
    top: 43%;
  }
}

.reviews__slider-btn-next {
  transform: rotate(180deg);
  right: -79px;

  @include vp-1199 {
    right: -69px;
  }

  @include vp-767 {
    right: -45px;
  }
}

.reviews__slider-btn-prev {
  left: -79px;

  @include vp-1199 {
    left: -69px;
  }

  @include vp-767 {
    left: -45px;
  }
}
